.form-visited {
  display: flex;
  flex-direction: column;
  padding-bottom: 39px;
  position: relative;
}
.form-visited .form-visited__block {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  margin-left: 20px;
}
.form-visited .form-visited__title {
  width: 236px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 20px;
}
.form-visited .form-visited__field {
  display: flex;
  align-items: center;
  column-gap: 16px;
}
.form-visited .form-visited__field .form-visited__field-label {
  font-size: 18px;
  font-weight: bold;
}
